import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiRequestListOptionsType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import {
  RequestGetData,
  RequestAddData,
  RequestEditData,
} from "@/kernel/api/dto/Request";

export const getRequestDelete = async (
  id: number
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/request/delete", {
    searchParams: {
      request_id: id,
    },
  });

  if (!res) {
    throw new NotRequestException(getRequestDelete.name);
  }

  return res;
};

export const getRequestGet = async (
  id: number
): Promise<ApiResponseType<RequestGetData>> => {
  const res = await getHttp().get<ApiResponseType<RequestGetData> | false>(
    "/request/get",
    {
      searchParams: {
        request_id: id,
      },
    }
  );

  if (!res) {
    throw new NotRequestException(getRequestGet.name);
  }

  return res;
};

export const getRequestList = async (
  params?: ApiRequestListOptionsType
): Promise<ApiResponseType<RequestGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<RequestGetData[]> | false>(
    "/request/list",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new NotRequestException(getRequestList.name);
  }

  return res;
};

export const postRequestAdd = async (
  payload: RequestAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/request/add", {
    ...payload,
    param: JSON.stringify(payload.param),
    class_rate: JSON.stringify(payload.class_rate),
    payoff_param: JSON.stringify(payload.payoff_param),
  });

  if (!res) {
    throw new NotRequestException(postRequestAdd.name);
  }

  return res;
};

export const postRequestEdit = async (
  payload: RequestEditData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/request/edit", {
    ...payload,
    param: JSON.stringify(payload.param),
    class_rate: JSON.stringify(payload.class_rate),
    payoff_param: JSON.stringify(payload.payoff_param),
  });

  if (!res) {
    throw new NotRequestException(postRequestEdit.name);
  }

  return res;
};
